import React, { FC } from "react";
import { useTypeFilter } from "../../hooks/useTypeFilter";
import withData from "../../HOC/withData";

const Layout: FC<any> = ({ state, dispatch, children }) => {
  // const [state, dispatch] = useTypeFilter();

  const handleSelect = e => {
    e.preventDefault();
    dispatch({ type: "setType", payload: e.target.value });
    dispatch({ type: "setList", payload: [] });
  };

  return (
    <div className="layout">
      <h3>SWAPI</h3>
      <nav>
        <label htmlFor="type">Type: &nbsp;</label>
        <select
          className="typeFilter"
          defaultValue={state.mainType}
          onChange={e => handleSelect(e)}
        >
          <option value="films">Movie</option>
          <option value="persons">Characters</option>
          <option value="starships">Starships</option>
        </select>
        <h4></h4>
        <label htmlFor="type">Set Filters: &nbsp;</label>
        <select
          className="filters"
          defaultValue={state.mainType}
          onChange={e => handleSelect(e)}
        >
          <option value="films">Movie</option>
          <option value="persons">Characters</option>
          <option value="starships">Starships</option>
        </select>
      </nav>
      {children}
      <style jsx>{`
        nav {
          display: flex;
          flex-direction: row;
        }

        h4 {
          border-right: solid 1px gray;
        }
      `}</style>
    </div>
  );
};

export default Layout;
